.Section_wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.Item_wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 65vw;
}

.Load_more {
  width: 50vw;
  padding: 2vw;
  background-color: blue;
  border: 1px solid blue;
  border-radius: 0.5vw;
  color: white;
  margin: 0 auto;
  margin-top: 5vw;
  margin-bottom: 5vw;
  font-size: 2vw;
  font-weight: 600;
}

.Load_more:active {
  background-color: lightblue;
  border: 1px solid lightblue;
  padding: 1.8vw;
}

.SelectPokemonsType {
  height: 3.5vw;
  width: 17vw;
  background-color: lightcoral;
  font-size: 2vw;
  margin-top: 2vw;
  border: none;
  border-radius: 10px;
  padding-left: 1.5vw;
  padding-right: 1.5vw;
}

.SelectPokemonsType option {
  border-bottom: 1px solid red;
}
